<template>
  <div class="form-box">
    <div class="box">登录</div>
    <el-input
      style="margin-bottom: 20px"
      placeholder="输入用户名"
      prefix-icon="el-icon-user"
      v-model="dataLogin.account"
    ></el-input>
    <el-input
      placeholder="输入密码"
      prefix-icon="el-icon-lock"
      v-model="dataLogin.password"
      show-password
    ></el-input>
    <el-button
      size="mini"
      style="margin-left: 39px; margin-top: 30px"
      @click="Login"
    >
      登录
    </el-button>
    <el-button size="mini" @click="register"> 注册 </el-button>
  </div>
</template>

<script>
import { reqLogin } from "@/assets/api";
export default {
  data() {
    return {
      dataLogin: {
        account: "",
        password: "",
        scene: "1",
        terminal: "3",
      },
    };
  },
  methods: {
    Login() {
      reqLogin(this.dataLogin)
        .then(({ data }) => {
          if (data.code == 1) {
            this.$router.push("/home");
          } else {
            this.$message.error(data.msg);
          }
        })
        .catch((err) => {
          console.log(err);
        });
    },
    register() {
      this.$router.push("/register");
    },
  },
};
</script>

<style lang="scss">
.box {
  margin: 10px 0;
  text-align: center;
  color: #fff;
  font-size: 30px;
}
.form-box {
  width: 200px;
  margin: 200px auto;
  padding: 100px;
  background: url("@/assets/images/tuzi.jpg");
  background-size: 100% 100%;
}
.el-input__inner {
  background: transparent !important;
  color: #fff;
  border: 1px solid #fff;
}
.el-button {
  border: 1px solid pink;
  background: pink !important;
  color: #fff;
}
input::-webkit-input-placeholder {
  color: #fff !important;
}
.el-icon-lock:before,
.el-icon-user:before {
  color: #fff !important;
}
</style>
